  font-size: 30px;
  line-height: 30px;
  text-align: center; }

.video__description {
  margin: 0;
  max-width: 340px;
  color: #ffffff;
  text-align: center;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  opacity: 0.7; }

.video__container {
  max-width: 929px;
  position: relative;
  margin-top: 55px; }

.video__button-play {
  width: 66px;
  height: 66px;
  background-color: #0ad9c6;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.2s; }
  .video__button-play:hover {
    background-color: #19bbab;
    transition: 0.2s; }
  .video__button-play::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 60%;
    transform: translate(-50%, -50%);
    border: 8px solid transparent;
    border-left: 10px solid #000000; }

.video__container-img {
  object-fit: cover;
  height: auto; }

